import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { useFormik } from 'formik';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Person4Icon from '@mui/icons-material/Person4';
import ContactPhoneIcon from '@mui/icons-material/ContactPhone';
import BadgeIcon from '@mui/icons-material/Badge';

import {
  DateTimeField,
  Loader,
  SearchField,
  SelectField,
  SelectMultiField,
  TextField,
} from '../components';

import {
  CoordinatorsQuery,
  DriversQuery,
  OwnersQuery,
  TruckQuery,
} from '../api';
import { useSnackbar } from '../hooks';
import {
  Owner,
  Coordinator,
  Driver,
  CreateTruck,
  TruckCertificates,
  TruckCrossborders,
  TruckEquipments,
  TruckStatuses,
  TruckTypes,
  TruckStatus,
  TruckCrossborder,
  TruckCertificate,
  TruckType,
} from '../utils/dataTypes';
import { getType } from '../utils/queryHelpers';

const initialValues = {
  truckNumber: undefined,
  status: 'Available' as TruckStatus,
  crossborder: 'Yes' as TruckCrossborder,
  certificate: 'Hazmat' as TruckCertificate,
  type: 'Cargo van' as TruckType,
  equipment: [],
  payload: undefined,
  grossWeight: undefined,
  make: undefined,
  model: undefined,
  year: undefined,
  color: undefined,
  vinCode: undefined,
  licencePlate: undefined,
  licenceState: undefined,
  plateExpires: undefined,
  insideDims: undefined,
  doorDims: undefined,
  validDims: undefined,
  owner: undefined,
  coordinator: undefined,
  driver: undefined,
};

export default function ViewTruckForm() {
  const [driverAddQueryParams, setDriverAddQueryParams] = React.useState<
    Record<string, string>
  >({});
  const [coordinatorAddQueryParams, setCoordinatorAddQueryParams] =
    React.useState<Record<string, string>>({});
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['truck', pathname.split('/')[3]],
    queryFn: TruckQuery,
    keepPreviousData: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateTruck>({
    initialValues: {
      ...initialValues,
      ...data,
    },
    enableReinitialize: true,
    onSubmit: () => {
      return;
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  const handleEdit = () => {
    navigate(pathname.replace('view', 'edit'), { replace: true });
  };

  return (
    <Box sx={{ height: '100%' }}>
      <form
        onSubmit={formik.handleSubmit}
        onReset={formik.handleReset}
        style={{ height: '100%' }}
      >
        <Box sx={{ p: 3, display: 'grid', height: '100%' }}>
          <Box
            key="controls"
            sx={{
              display: 'inline-flex',
              height: '100%',
              overflowX: 'hidden',
              overflowY: 'scroll',
            }}
          >
            <Box key="left" sx={{ p: 1, width: '50%' }}>
              <TextField
                fieldName="truckNumber"
                label="Truck Number"
                disabled
                formik={formik}
              />
              <SelectField
                fieldName="crossborder"
                label="Cross Border"
                items={TruckCrossborders}
                disabled
                formik={formik}
              />
              <SelectField
                fieldName="type"
                label="Type"
                items={TruckTypes}
                disabled
                formik={formik}
              />
              <TextField
                fieldName="payload"
                label="Payload"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="make"
                label="Make"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="year"
                label="Year"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="vinCode"
                label="Vin Code"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="licenceState"
                label="Licence State"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="insideDims"
                label="Inside Dims"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="validDims"
                label="Valid Dims"
                disabled
                formik={formik}
              />
              <SearchField<Owner>
                fieldName="owner"
                label="Owner"
                getSelectedLabel={(selectedItem) =>
                  `${selectedItem.fullName}, ${getType(selectedItem)}, ${
                    selectedItem.phone
                  }`
                }
                getItemLabel={(item) => `${item.fullName}`}
                getItemSecondLabel={(item) => `${getType(item)}, ${item.phone}`}
                ListIcon={Person4Icon}
                queryFn={OwnersQuery}
                startEmpty={false}
                orderby="fullName"
                order="asc"
                disabled
                formik={formik}
              />
            </Box>
            <Box key="right" sx={{ p: 1, width: '50%' }}>
              <SelectField
                fieldName="status"
                label="Status"
                items={TruckStatuses}
                disabled
                formik={formik}
              />
              <SelectField
                fieldName="certificate"
                label="Certificate"
                items={TruckCertificates}
                disabled
                formik={formik}
              />
              <SelectMultiField
                fieldName="equipment"
                label="Equipment"
                items={TruckEquipments}
                disabled
                formik={formik}
              />
              <TextField
                fieldName="grossWeight"
                label="Gross Weight"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="model"
                label="Model"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="color"
                label="Color"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="licencePlate"
                label="Licence Plate"
                disabled
                formik={formik}
              />
              <DateTimeField
                fieldName="plateExpires"
                label="Plate Expires"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="doorDims"
                label="Door Dims"
                disabled
                formik={formik}
              />
              <SearchField<Coordinator>
                fieldName="coordinator"
                label="Coordinator"
                getSelectedLabel={(selectedItem) =>
                  `${selectedItem.fullName}, ${getType(selectedItem)}, ${
                    selectedItem.phone
                  }`
                }
                getItemLabel={(item) => `${item.fullName}`}
                getItemSecondLabel={(item) => `${getType(item)}, ${item.phone}`}
                ListIcon={ContactPhoneIcon}
                queryFn={CoordinatorsQuery}
                startEmpty={false}
                orderby="fullName"
                order="asc"
                additionalQueryParams={coordinatorAddQueryParams}
                disabled
                formik={formik}
              />
              <SearchField<Driver>
                fieldName="driver"
                label="Driver"
                getSelectedLabel={(selectedItem) =>
                  `${selectedItem.fullName}, ${getType(selectedItem)}, ${
                    selectedItem.phone
                  }`
                }
                getItemLabel={(item) =>
                  `${item.fullName}${
                    item.driveTrucks && item.driveTrucks[0]
                      ? ', On Truck: ' + item.driveTrucks[0].truckNumber
                      : ''
                  }`
                }
                getItemSecondLabel={(item) => `${getType(item)}, ${item.phone}`}
                ListIcon={BadgeIcon}
                queryFn={DriversQuery}
                startEmpty={false}
                orderby="fullName"
                order="asc"
                additionalQueryParams={driverAddQueryParams}
                disabled
                formik={formik}
              />
            </Box>
          </Box>
          <Box key="buttons" sx={{ paddingTop: 3, display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleEdit}
              >
                Edit
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={loading || fetching} />
    </Box>
  );
}
